.Loader {
  font-size: 200px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.ContactIcon {
  border-radius: 50%;
  color: white;
  text-align: center;
  vertical-align: middle;
  position: relative;
  margin-right: 15px; }
  .ContactIcon .content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%); }

.ContactEntry {
  border: solid black 3px;
  padding: 15px;
  margin-top: 20px;
  border-radius: 15px;
  width: 80vw;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: white;
  cursor: pointer; }

.RoundIcon {
  border: solid black 3px;
  background-color: white;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 28px;
  display: inline-block;
  text-align: center; }
  .RoundIcon .content {
    vertical-align: middle; }

.wrap-round-icon {
  width: 46px; }

.Email {
  display: block;
  color: black;
  text-align: left;
  width: 500px;
  margin-top: 10px;
  margin-left: 15%; }
  .Email .email-icon {
    margin-right: 10px; }

.Phone {
  display: block;
  color: black;
  text-align: left;
  width: 500px;
  margin-top: 10px;
  margin-left: 15%; }
  .Phone .phone-icon {
    margin-right: 10px; }

.ContactDetail {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px; }
  .ContactDetail .contact-detail-content {
    position: absolute;
    top: 50px;
    left: 50px;
    right: 50px;
    bottom: 50px;
    border: solid black 5px;
    background-color: white;
    text-align: center; }

.App {
  background-color: lightgrey; }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: lightgrey;
}

